.Location {
	#map {
		width: 100%;
		height: 500px;
		background: #000;
	}
	.btnSet {
		display: flex;
		flex-wrap: wrap;
		align-content: flex-start;
		justify-content: space-between;
		margin-top: 50px;

		button {
			cursor: pointer;
		}

		ul {
			display: flex;
			li {
				min-width: 160px;
				margin: 0 10px;
				cursor: pointer;
				position: relative;
				padding: 10px 20px;
				color: #333;
				background: transparent;
				border: none;
				font-size: 16px;
				text-align: center;
				letter-spacing: -1px;
				text-decoration: none;
				text-transform: uppercase;
				overflow: hidden;
				transition: 0.5s;
				height: 48px;
				line-height: 25px;
				font-weight: bold !important;
				background: #333;
				color: #fff;
				border-radius: 5px;
				&:hover {
					box-shadow: inset 0 0 0 2em #fff;
					outline: 1px solid #333;
					color: #333;
				}
				&.on {
					background: #d83a3a;
					border: 1px solid #d83a3a;
					color: #fff;
					&:hover {
						box-shadow: inset 0 0 0 2em #fff;
						outline: 1px solid #333;
						color: #333;
					}
				}
			}
		}
	}
}
